:root {
  --mainText: #3a2e8d;
  --secondaryText: #fff;
  --yellowText: #e8b35d;
  --brownText: #542502;
  --background: rgba(58, 46, 141, .68);
}

.header-page {
  width: 100%;
  background-color: rgba(58, 46, 141, .69);
  margin: 0 auto;
  padding: 10px 0;
  position: fixed;
}

.header-container {
  justify-content: space-between;
  display: flex;
}

.header-logo {
  transition: transform .3s ease-in-out;
}

.header-logo:hover, .header-logo:focus {
  transform: scale(1.1);
}

.header-list {
  display: none;
}

.header-link {
  text-align: center;
  letter-spacing: .02em;
  color: var(--secondaryText);
  font-size: 18px;
  font-weight: 500;
  line-height: 1.07;
  transition: color .3s ease-in-out;
}

.header-link:hover, .header-link:focus {
  color: var(--yellowText);
}

.menu-toggle {
  background-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.header-icon {
  stroke: var(--secondaryText);
  transition: stroke .3s ease-in-out;
}

.header-icon:hover, .header-icon:focus {
  stroke: var(--yellowText);
}

@media screen and (min-width: 768px) {
  .header-logo {
    width: 66px;
    height: 80px;
  }

  .header-page {
    padding: 20px 0;
  }

  .menu-toggle {
    display: none;
  }

  .header-list {
    align-items: center;
    gap: 35px;
    display: flex;
  }
}

@media screen and (min-width: 1280px) {
  .header-link {
    text-align: center;
    letter-spacing: .02em;
    color: var(--secondaryText);
    font-size: 20px;
    font-weight: 700;
    line-height: 1.07;
    transition: color .3s ease-in-out;
  }

  .header-list {
    gap: 50px;
  }
}

.menu-icon-x {
  fill: var(--secondaryText);
  background-color: rgba(0, 0, 0, 0);
  justify-content: center;
  align-items: center;
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 24px;
  right: 24px;
}

.menu-icon-x:focus, .menu-icon-x:active {
  fill: var(--yellowText);
}

.menu-container {
  width: 100vw;
  height: 100vh;
  background-color: var(--mainText);
  z-index: 999;
  flex-direction: column;
  justify-content: space-between;
  padding: 80px 24px 40px 40px;
  transition: transform .25s ease-in-out;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(100%);
}

.menu-container.is-open {
  transform: translateX(0);
}

.menu-container .menu-toggle {
  color: #fff;
  position: absolute;
  top: 16px;
  right: 16px;
}

.mobile-menu-list {
  color: #fff;
  display: block;
}

.mobile-menu-list:not(:last-child) {
